body {
  font-family: $font-stack;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.03em;
  color: #161616;
  margin: 0;
}

::marker {
  display: none;
}

summary {
  list-style: none
}

h1 {
  font-weight: 700;
  font-size: 36px;
  line-height: 150%;
  margin: 0;
}

h2 {
  position: relative;
  display: flex;
  align-items: center;
  width: fit-content;
  margin: 0 auto 46px auto;
  font-weight: 700;
  font-size: 30px;
  line-height: 150%;
  padding-left: 34px;
}

h3 {
  font-weight: 700;
  margin: 0;
  font-size: 20px;
  line-height: 150%;
}

h2::before {
  content: '';
  position: absolute;
  background: linear-gradient(90deg, #B41D24 8.33%, #FAFAFA 100%);
  width: 14px;
  height: 14px;
  border-radius: 100%;
  left: 0;
}

a {
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}

a,
button {
  cursor: pointer;
}

section:not(section.first) {
  margin-top: 100px;
}

.container {
  width: 1200px;
  margin: auto;
}

#menu__toggle {
  display: none;
}

header {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  height: 81px;
  padding: 15px 0;
  z-index: 2;

  .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  ul {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;

    li {
      list-style-type: none;
      transition: .4s;

      a {
        font-size: 16px;
        line-height: 22px;
        transition: .4s;
        border-bottom: 1px solid transparent;
      }
    }

    li:not(:last-child) {
      margin-right: 40px;
    }

    li:hover a {
      color: #B41D24;
      transition: .4s;
    }
  }

  .header__right {
    display: flex;
    align-items: flex-end;
    flex-direction: column;

    .header__phone {
      font-weight: 700;
      font-size: 20px;
      line-height: 27px;
      color: #B41D24;
    }

    div {
      font-size: 15px;
      line-height: 20px;
    }
  }
}

.first {
  background: url('/img/first-back.png') no-repeat;
  background-size: cover;
  background-position: center;
  height: 670px;
  margin-top: 81px;

  .container {
    height: 100%;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;


    &--text {
      display: flex;
      flex-direction: column;
      justify-content: center;
      width: 50%;
      height: 100%;

      h1 {
        margin-bottom: 36px;
      }

      ul {
        margin: 0;
        padding: 0;
        margin-bottom: 27px;

        li {
          display: flex;
          align-items: center;
          list-style-type: none;

          img {
            margin: 0 21px;
          }
        }

        li:not(:last-child) {
          margin-bottom: 15px;
        }
      }
    }

    &>img {
      width: 50%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.btn {
  display: block;
  position: relative;
  width: fit-content;
  font-weight: 700;
  font-size: 16px;
  line-height: 22px;
  color: #FFFFFF;
  padding: 19px 59px;
  box-sizing: border-box;
  background: #B41D24;
  border-radius: 10px;
  transition: .4s;
}

.btn:hover {
  top: -1px;
  -webkit-box-shadow: 0px 5px 10px 2px rgba(180, 29, 36, 0.2);
  -moz-box-shadow: 0px 5px 10px 2px rgba(180, 29, 36, 0.2);
  box-shadow: 0px 5px 10px 2px rgba(180, 29, 36, 0.2);
  transition: .4s;
}

.calculator {


  &__service {

    &_wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &--item {
        display: flex;
        align-items: flex-start;
        background: #F4F1F1;
        border: 1px solid rgba(0, 0, 0, 0.15);
        border-radius: 10px;
        box-sizing: border-box;
        padding: 48px 35px 48px 35px;
        width: 504px;

        .img {
          width: 135px;
          height: 135px;
          background: #FCFCFC;
          border-radius: 10px;
          padding: 25px;
          box-sizing: border-box;
          position: relative;
          margin-right: 30px;

          .active {
            background: linear-gradient(0deg, rgba(114, 47, 47, 0.3), rgba(114, 47, 47, 0.3));
            position: absolute;
            top: 25px;
            bottom: 25px;
            left: 25px;
            right: 25px;
            margin: auto;
          }
        }

        h3 {
          border-bottom: 1px solid rgba(0, 0, 0, 0.15);
          padding-bottom: 8px;
          margin-bottom: 22px;
          font-size: 24px;
          line-height: 150%;
        }

        .txt {
          p {
            margin: 0;
          }

          p:not(:last-child) {
            margin-bottom: 8px;
          }

          a {
            font-weight: 700;
            font-size: 16px;
            line-height: 22px;
            color: #B41D24;
            margin-top: 22px;
            display: flex;
            align-items: center;

            img {
              margin-left: 10px;
              transition: .4s;
            }
          }

          a:hover {
            img {
              margin-left: 15px;
              transition: .4s;
            }
          }
        }
      }
    }
  }

  &__calc {
    background: rgba(229, 230, 234, 0.8);
    border: 1px solid rgba(0, 0, 0, 0.15);
    box-sizing: border-box;
    border-radius: 10px;
    margin-top: 32px;
    padding: 53px 0;
    position: relative;
    overflow: hidden;

    .close {
      background-image: url("data:image/svg+xml,%3Csvg width='20' height='20' viewBox='0 0 20 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.999023 0.999878L18.999 18.9999' stroke='black' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M0.999023 18.9999L18.999 0.999878' stroke='black' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
      background-position: center;
      background-size: contain;
      background-repeat: no-repeat;
      width: 18px;
      height: 18px;
      position: absolute;
      top: 45px;
      cursor: pointer;
      right: 45px;
    }

    &::before {
      content: '';
      position: absolute;
      background: url('/img/decor-left.png') no-repeat;
      width: 235px;
      height: 235px;
      left: 0;
      bottom: 0;
    }

    &::after {
      content: '';
      position: absolute;
      background: url('/img/decor-right.png') no-repeat;
      width: 135px;
      height: 274px;
      right: -10px;
      top: 107px;
    }

    .btn {
      margin: 0 auto;
      margin-top: 40px;
    }

    h3 {
      margin: auto;
      width: fit-content;
      border-bottom: 1px solid rgba(0, 0, 0, 0.15);
      padding-bottom: 15px;
      margin-bottom: 22px;
      font-size: 24px;
    }

    &--wrapper {
      display: flex;
      align-items: flex-start;
      margin: auto;
      flex-direction: column;
      max-width: 330px;

    }

    &--info {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 36px;

      div {
        font-size: 20px;
      }
    }

    &--radio {

      label {
        display: flex;
        align-items: center;
        cursor: pointer;
        width: fit-content;

        input[type=radio] {
          -moz-appearance: none;
          -webkit-appearance: none;
          -o-appearance: none;
          content: none;
          outline: none;
          cursor: pointer;
          margin: 0;
        }

        input[type=radio]:before {
          box-sizing: border-box;
          content: "";
          display: block;
          width: 30px;
          border-radius: 100%;
          height: 30px;
          left: 800px;
          top: 1534px;
          background: rgba(255, 255, 255, 0.7);
          box-shadow: 0px 2px 16px rgba(0, 0, 0, 0.25);
          border: 9px solid rgba(255, 255, 255, 0.7);

        }

        input[type=radio]:checked:before {
          // background-color: #B41D24 !important;
          border: 9px solid rgba(255, 255, 255, 1);
          background: #B41D24;
        }

        div {
          font-weight: 600;
          font-size: 18px;
          margin: 0 10px 0 22px;
        }
      }

      label:not(:last-child) {
        margin-bottom: 36px;
      }
      .question {
        background-image: url("data:image/svg+xml,%3Csvg width='23' height='24' viewBox='0 0 23 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.5' clip-path='url(%23clip0_35:138)'%3E%3Cpath d='M11.4995 1.00513C5.14816 1.00513 -0.000488281 6.15377 -0.000488281 12.5051C-0.000488281 18.8565 5.14816 24.0056 11.4995 24.0056C17.8509 24.0056 22.9995 18.8565 22.9995 12.5051C22.9995 6.15377 17.8509 1.00513 11.4995 1.00513ZM11.4995 22.1488C6.20664 22.1488 1.91618 17.8579 1.91618 12.565C1.91618 7.27215 6.20664 2.98169 11.4995 2.98169C16.7924 2.98169 21.0828 7.27215 21.0828 12.565C21.0828 17.8579 16.7924 22.1488 11.4995 22.1488Z' fill='%23231F20'/%3E%3Cpath d='M10.9159 16.3031C10.0903 16.3031 9.67773 16.7272 9.67773 17.5748C9.67773 17.9845 9.78459 18.3031 9.99782 18.5303C10.211 18.7574 10.5172 18.8719 10.9159 18.8719C11.315 18.8719 11.6217 18.755 11.8378 18.5221C12.0539 18.2892 12.1627 17.9735 12.1627 17.5748C12.1627 17.1704 12.0558 16.857 11.8426 16.6352C11.6294 16.4133 11.3203 16.3031 10.9159 16.3031Z' fill='%23231F20'/%3E%3Cpath d='M14.0577 6.94506C13.4117 6.406 12.5387 6.13623 11.4385 6.13623C10.3446 6.13623 9.3096 6.36288 8.33066 6.80994C7.78154 7.05719 8.02783 7.61781 8.02879 7.62069L8.24825 8.08021C8.24825 8.08021 8.50748 8.51961 9.06619 8.3035C9.07194 8.30158 9.07385 8.30254 9.07912 8.30015C9.32062 8.19904 9.565 8.10513 9.81321 8.02223C10.2679 7.87081 10.762 7.79463 11.2957 7.79463C11.8851 7.79463 12.337 7.92113 12.6518 8.17365C12.9666 8.42617 13.1233 8.78842 13.1233 9.2604C13.1233 9.66481 13.026 10.0127 12.8324 10.305C12.6389 10.5973 12.2081 11.007 11.5401 11.5345C10.979 11.9725 10.5856 12.3965 10.3609 12.8062C10.1362 13.2164 10.024 13.7305 10.024 14.3482C10.0025 14.7976 10.5751 14.8873 10.5751 14.8873H10.921C10.921 14.8873 11.5794 14.8911 11.68 14.3237C11.6815 14.3161 11.6839 14.3137 11.6853 14.306C11.7083 13.9466 11.7907 13.6491 11.9397 13.4215C12.1166 13.1517 12.4668 12.812 12.9886 12.4018C13.6005 11.9246 14.0313 11.5369 14.281 11.2394C14.5306 1