        background-image: url("data:image/svg+xml,%3Csvg width='23' height='24' viewBox='0 0 23 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg opacity='0.5' clip-path='url(%23clip0_35:138)'%3E%3Cpath d='M11.4995 1.00513C5.14816 1.00513 -0.000488281 6.15377 -0.000488281 12.5051C-0.000488281 18.8565 5.14816 24.0056 11.4995 24.0056C17.8509 24.0056 22.9995 18.8565 22.9995 12.5051C22.9995 6.15377 17.8509 1.00513 11.4995 1.00513ZM11.4995 22.1488C6.20664 22.1488 1.91618 17.8579 1.91618 12.565C1.91618 7.27215 6.20664 2.98169 11.4995 2.98169C16.7924 2.98169 21.0828 7.27215 21.0828 12.565C21.0828 17.8579 16.7924 22.1488 11.4995 22.1488Z' fill='%23B41D24'/%3E%3Cpath d='M10.9159 16.3031C10.0903 16.3031 9.67773 16.7272 9.67773 17.5748C9.67773 17.9845 9.78459 18.3031 9.99782 18.5303C10.211 18.7574 10.5172 18.8719 10.9159 18.8719C11.315 18.8719 11.6217 18.755 11.8378 18.5221C12.0539 18.2892 12.1627 17.9735 12.1627 17.5748C12.1627 17.1704 12.0558 16.857 11.8426 16.6352C11.6294 16.4133 11.3203 16.3031 10.9159 16.3031Z' fill='%23B41D24'/%3E%3Cpath d='M14.0577 6.94506C13.4117 6.406 12.5387 6.13623 11.4385 6.13623C10.3446 6.13623 9.3096 6.36288 8.33066 6.80994C7.78154 7.05719 8.02783 7.61781 8.02879 7.62069L8.24825 8.08021C8.24825 8.08021 8.50748 8.51961 9.06619 8.3035C9.07194 8.30158 9.07385 8.30254 9.07912 8.30015C9.32062 8.19904 9.565 8.10513 9.81321 8.02223C10.2679 7.87081 10.762 7.79463 11.2957 7.79463C11.8851 7.79463 12.337 7.92113 12.6518 8.17365C12.9666 8.42617 13.1233 8.78842 13.1233 9.2604C13.1233 9.66481 13.026 10.0127 12.8324 10.305C12.6389 10.5973 12.2081 11.007 11.5401 11.5345C10.979 11.9725 10.5856 12.3965 10.3609 12.8062C10.1362 13.2164 10.024 13.7305 10.024 14.3482C10.0025 14.7976 10.5751 14.8873 10.5751 14.8873H10.921C10.921 14.8873 11.5794 14.8911 11.68 14.3237C11.6815 14.3161 11.6839 14.3137 11.6853 14.306C11.7083 13.9466 11.7907 13.6491 11.9397 13.4215C12.1166 13.1517 12.4668 12.812 12.9886 12.4018C13.6005 11.9246 14.0313 11.5369 14.281 11.2394C14.5306 1