        }
        
      }

      label[for="type-2"] {
        position: relative;

        .question:hover~.question-info {
          display: block;
        }
      }

      label[for="type-3"] {
        position: relative;

        .question:hover~.question-info {
          display: block;
        }
      }

      .question-info {
        position: absolute;
        display: none;
        background: #F4F1F1;
        border-radius: 10px;
        right: -100%;
        top: 100%;
        padding: 14px 29px;
        box-sizing: border-box;
        width: 320px;
        z-index: 2;

        p {
          font-weight: 400;
          font-size: 14px;
          line-height: 21px;
          margin: 0;
        }
      }
