    display: none !important;
  }

  details[open] summary~* {
    animation: sweep .5s ease-in-out;
  }

  @keyframes sweep {
    0% {
      opacity: 0;
      margin-top: -10px
    }

    100% {
      opacity: 1;
      margin-top: 0px
    }
  }

  details summary~* {
    animation: sweep2 .5s ease-in-out;
  }

  @keyframes sweep2 {
    0% {
      opacity: 1;
      margin-top: 0px
    }

    100% {
      opacity: 0;
      margin-top: -10px
    }

  }

  details:last-child {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);

