.contact {
  background: url('/img/contact-back.png') no-repeat;
  background-size: cover;
  background-position: center;
  background-color: #F4F1F1;

  h2 {
    font-size: 25px;
    line-height: 34px;
    padding-left: 0;
    margin: 0;
    margin-bottom: 10px;
  }

  h2::before {
    display: none;
  }

  .container {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }

  &__wrapper {
    width: 50%;
    padding-top: 55px;
    padding-bottom: 51px;
    box-sizing: border-box;

    &>p {
      display: block;
      margin: 0;
      margin-bottom: 22px;
    }

    form {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      height: 100%;

      input:not(input[type="checkbox"]) {
        position: relative;
        outline: none;
        font-family: $font-stack;
        width: 100%;
        box-sizing: border-box;
        background: transparent;
        height: 38px;
        font-size: 14px;
        line-height: 19px;
        color: rgba(22, 22, 22, .5);
        padding: 12px 0 12px 40px;
        border: 0;
        border-bottom: 1px solid #161616;
      }

      input::placeholder,
      textarea::placeholder {
        font-family: $font-stack;
        font-size: 14px;
        line-height: 19px;
        color: rgba(22, 22, 22, .5);
      }

      #input-phone {
        margin: 22px 0;
      }

      #input-mail {
        margin-bottom: 68px;
      }

      button {
        border: 0;
      }

      textarea {
        outline: none;
        font-family: $font-stack;
        overflow: hidden;
        width: 100%;
        resize: none;
        box-sizing: border-box;
        background: transparent;
        height: 38px;
        font-size: 14px;
        line-height: 19px;
        color: rgba(22, 22, 22, .5);
        padding: 12px 0 12px 0;
        border: 0;
        border-bottom: 1px solid #161616;
        margin-bottom: 22px;
      }

      label {
        margin-bottom: 36px;
        font-size: 16px;
        line-height: 22px;
        color: #000000;
        cursor: pointer;
        display: flex;
      }

