//media

@media (max-width: 1230px) {

  .container,
  header {
    padding: 0 15px;
    width: 100%;
    box-sizing: border-box;
  }

  header .container {
    padding: 0;
  }

  // .hamburger-menu {
  //   display: none !important;
  // }

  /* скрываем чекбокс */
  #menu__toggle {
    opacity: 0;
    display: none;
  }

  /* стилизуем кнопку */
  .menu__btn {
    display: flex;
    /* используем flex для центрирования содержимого */
    align-items: center;
    /* центрируем содержимое кнопки */
    position: relative;
    // top: 20px;
    // left: 20px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
  }

  /* добавляем "гамбургер" */
  .menu__btn>span,
  .menu__btn>span::before,
  .menu__btn>span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #616161;
  }

  .menu__btn>span::before {
    content: '';
    top: -8px;
  }

  .menu__btn>span::after {
    content: '';
    top: 8px;
  }

  /* контейнер меню */
  .menu__box {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    left: -100%;
    width: 300px;
    height: 100%;
    margin: 0;
    padding: 80px 0;
    list-style: none;
    text-align: center;
    background-color: #ECEFF1;
    box-shadow: 1px 0px 6px rgba(0, 0, 0, .2);
  }

  /* элементы меню */
  .menu__item {
    display: block;
    padding: 12px 24px;
    color: #333;
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
  }

  .menu__item:hover {
    background-color: #CFD8DC;
  }

  #menu__toggle:checked~.menu__btn>span {
    transform: rotate(45deg);
  }

  #menu__toggle:checked~.menu__btn>span::before {
    top: 0;
    transform: rotate(0);
  }

  #menu__toggle:checked~.menu__btn>span::after {
    top: 0;
    transform: rotate(90deg);
  }

  #menu__toggle:checked~.menu__box {
    visibility: visible;
    left: 0;
  }

  .menu__btn>span,
  .menu__btn>span::before,
  .menu__btn>span::after {
    transition-duration: .25s;
  }

  .menu__box {
    transition-duration: .25s;
  }

  .menu__item {
    transition-duration: .25s;
  }

  header .header__right {
    align-items: center;
    display: flex;
    flex-direction: row;
  }

  .header__right div {
    display: none;
  }

  .hamburger-menu {
    order: 3;
  }

  .first__wrapper>img {
    display: none;
  }

  .first__wrapper--text {
    width: 100%;
  }

  .first {
    padding: 50px;
    height: fit-content;
  }

  section:not(section.first) {
    margin-top: 50px;
  }

  header {
    display: flex;
  }

  header ul li:not(:last-child) {
    margin-right: 0;
  }
}

@media (max-width: 1050px) {
  .calculator__service_wrapper--item {
    width: 100%;
  }

  .calculator__service_wrapper--item:not(:last-child) {
    margin-bottom: 20px;
  }

  .calculator__service_wrapper {
    flex-wrap: wrap;
  }

  .contact__img {
    display: none;
  }

  .contact__wrapper {
    width: 100%;
  }

  .scheme__wrapper--item:first-child:after,
  .scheme__wrapper--item:nth-child(2):after,
  .scheme__wrapper--item:nth-child(3):after {
    display: none;
  }

  .scheme__wrapper--item:nth-child(2n) {
    margin-top: 0;
  }

  .scheme__wrapper--item {
    box-sizing: border-box;
    margin: 0 15px;
    position: relative;
    width: calc(50% - 30px);
  }

  .scheme__wrapper {
    flex-wrap: wrap;
    margin: 0 -15px;
  }

}

@media (max-width: 680px) {
  .calculator__calc .close {
    top: 10px;
    right: 10px;
  }

  h2 {
    margin: 0 auto 23px;
    font-size: 24px;
    padding-left: 24px;
  }

  h1,
  h2 {
    line-height: 120%;
  }

  .scheme h2 {
    margin-bottom: 40px;
  }

  .first {
    padding: 50px 0;
  }

  .modal-content {
    max-width: 90%;
    padding: 20px 20px;
  }

  .calculator__calc {
    padding: 35px 15px;
  }
}

@media (max-width: 500px) {
  .header__phone {
    display: none;
  }

  .scheme__wrapper--item {
    margin: 0;
    width: 100%;
  }

  .scheme__wrapper {
    margin: 0;
  }

  h1 {
    font-size: 30px;
  }

  .calculator__service_wrapper--item h3 {
    font-size: 20px;
    line-height: 145%;
  }

  .calculator__service_wrapper--item {
    padding: 24px 17px;
  }

  .faq summary {
    padding-right: 50px;
  }

  .calculator__service_wrapper--item {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .calculator__service_wrapper--item .img {
    margin-right: 0;
    margin-bottom: 20px;
  }

  .first__wrapper--text ul li {
    font-size: 14px;
  }

  .first__wrapper--text ul li img {
    margin: 0 10px;
  }


}

@media (max-width: 370px) {
  .first__wrapper--text ul li img {
    display: none;
  }

  .first__wrapper--text ul li div {
    margin-right: 5px;
  }

  .calculator__calc::after,
  .calculator__calc::before {
    display: none;
  }

  .calculator__calc--radio label div {
    font-size: 14px;
    margin: 0 5px 0 11px;
  }

  .calculator__calc--radio label input[type=radio]:before {
    width: 20px;
    height: 20px;
  }

  .calculator__calc--radio .question-info {
    padding: 7px 15px;
    right: 0;
    left: 0;
    top: 100%;
    width: 290px;
    height: auto;
  }

  .calculator__calc--radio label input[type=radio]:checked:before {
    border: 5px solid #fff;
  }

  .calculator__calc--info div {
    font-size: 16px;
  }

  .calculator__calc h3 {
    font-size: 21px;
    line-height: 110%;
  }

  .faq summary {
    font-size: 16px;
  }

  .btn {
    padding: 19px 45px;
  }
}